@import "vendor/normalize";

// @import "vendor/owl-carousel";

// sprite icons @extend .icon-.....
@import "sprite/sprite";

@import "partials/partial";

@font-face {
	font-family: "SFNS-Regular";
	src: url("../fonts/SFNS/San_Francisco_Display_Regular.otf") format("opentype");
}

@font-face {
	font-family: "SFNS-Bold";
	src: url("../fonts/SFNS/San_Francisco_Display_Bold.otf") format("opentype");
}

@font-face {
	font-family: "SFNS-Thin";
	src: url("../fonts/SFNS/San_Francisco_Display_Thin.otf") format("opentype");
}

body {
	width: 100%;
	overflow-x: hidden;
	margin: 0;
	padding: 0;
	background: #fff;
	color: #333;
	font-family: 'SFNS-Regular','SFNS-Bold','SFNS-Thin', sans-serif;
	font-size: 14px;
	line-height: auto;
}

.container {
	width: 320px;
	margin: 0 auto;
	max-width: 100%;
}

$mob-font-regular: "SFNS-Regular", sans-serif;
$mob-font-bold: "SFNS-Bold", sans-serif;
$mob-font-thin: "SFNS-Thin", sans-serif;
$Bghover-color: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);

.header_height_increase {
	margin-bottom: 340px;
}

.home_header {
	width: 100%;
	height: 58px !important;
	display: inline-block;

	.home_header_top {
		display: none;
	}

	.home_header_bottom {
		height: 60px;
		width: 100%;
	}
}

.home_header_bottom_languagepicker,
.home_header_bottom_help {
	display: none;
}

.home_header_bottom_logo {
	top: 10px !important;
	left: 3px !important;
	width: 153px !important;
	height: 40.3px !important;
}

.home_header_bottom .home_header_bottom_nav {
	display: none;
	top: 50px;
	left: -20px;
	width: 100%;
	font-family: $mob-font-regular;
	font-weight: normal;

	li {
		width: 100%;
		margin-bottom: 20px;
		border-bottom: 1px solid rgba(165, 165, 165, 0.47);
		padding-bottom: 10px;
	}

	sup {
		top: -10px;
		left: 65px;
	}
}

.home_header_bottom_nav_trigger {
	float: right;
	position: relative;
	display: block !important;
	height: 60px !important;
	width: 25.61px;
	background: #FBFBFB;
	margin-top: 20px;
	margin-right: 15.26px;
	cursor: pointer;

	span {
		display: inline-block;
		position: absolute;
		height: 1.71px;
		width: 25.61px;
		background: #424343 !important;
	}

	span:nth-of-type(1) {
		top: 0;
	}

	span:nth-of-type(2) {
		top: 7px;
	}

	span:nth-of-type(3) {
		top: 14px;
	}
}

.home_header_bottom_nav_trigger:hover {
	.bar1 {
		transform: rotate(-45deg) translate(-4px, -1px);
		transition: ease-in 0.3s;
	}

	.bar2 {
		opacity: 0;
	}

	.bar3 {
		transform: rotate(45deg) translate(-10px, -7px);
		transition: ease-in 0.3s;
	}
}

.home_header_nav_menu {
	position: relative;
	display: block;
}

.home_header_nav_menu:hover .home_header_bottom_nav,
.home_header_nav_menu:focus .home_header_bottom_nav {
	display: block;
}

.main {
	margin-top: 0;
	width: 100%;
	font-family: $mob-font-regular;

	h1.main_title {
		height: 102px;
		width: 290px;
		color: #FFFFFF;
		font-family: $mob-font-regular;
		font-size: 24px;
		line-height: 34px;
		margin-top: 99px;
		margin-left: 15px;
		word-spacing: 2.5px;
		font-weight: normal;
		float: none;
	}

	.main_title_tip_more {
		left: 15px !important;
		top: 241px !important;
		font-family: $mob-font-thin !important;
	}
}

.main_title_tip {
	display: none;
}

.main_help_block {
	width: 290px !important;
	height: 599px !important;
	float: none;
	margin-left: 15px;
	margin-top: 141px;
	box-sizing: border-box;

	.main_help_block_title {
		height: 26px;
		width: 98px;
		color: #333;
		font-family: $mob-font-regular;
		font-weight: normal;
		font-size: 18px;
		line-height: 26px;
		margin-left: 22px;
	}

	input[type=text],
	input[type=email],
	input[type=tel] {
		width: 250px !important;
		margin-left: 21px;
		font-family: $mob-font-thin !important;
		font-weight: normal;
	}
}

input[type=text]:nth-of-type(1) {
	margin-bottom: 55px;
}

input[type=email] {
	padding-bottom: 5px;
	margin-bottom: 20px;
}

.main_help_block input::placeholder {
	color: #A5A5A5;
	word-spacing: 1px;
	letter-spacing: 0.3px;
}

.main_help_block input:-ms-input-placeholder { /* Internet Explorer 10-11 */
	color: #A5A5A5;
	word-spacing: 1px;
	letter-spacing: 1px;
}

.main_help_block input::-ms-input-placeholder { /* Microsoft Edge */
	color: #A5A5A5;
	word-spacing: 1px;
	letter-spacing: 1px;
}

.form_money_amount {
	top: 153px !important;
	left: 21px;
	font-family: $mob-font-regular;

	span {
		padding: 15px 30px;
	}
}

.main_help_block .btn {
	width: 125px;
	margin-top: 20px;
}

.form_single {
	margin-left: 21px;
}

.form_month {
	margin-top: -45px !important;
	margin-right: 21px;
}

.main_help_block label:nth-of-type(1) {
	position: absolute;
	top: 366px;
	left: 48px;
}

.main_help_block label:nth-of-type(2) {
	position: absolute;
	top: 367px;
	right: 43px;
}

.main_help_block label:last-of-type {
	bottom: 96px;
	right: 69px;
	font-family: $mob-font-thin;
	font-weight: none;
	letter-spacing: 1px;
	white-space: nowrap;
}

.main_help_block_tip {
	min-width: 0 !important;
	width: 219px;
	height: 32px;
	font-family: $mob-font-regular;
	font-weight: none;
	margin-top: 20px;
	margin-left: 22px;
	font-size: 12px;
	line-height: 16px;
	letter-spacing: 0.3px;
}

.main_help_block_help {
	margin-top: 96px;
	font-family: $mob-font-regular;
	font-weight: normal;
	margin-left: 21px;
}

.main_help_block input[type='checkbox'] {
	float: none;
	position: absolute;
	margin: unset;
	top: 489px;
	left: 21px;
}
